.color-squares {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .color-square {
    width: 50px;
    height: 50px;
    margin: 5px;
    cursor: pointer;
  }
  
  button {
    font-size: 16px;
    padding: 10px 20px;
    background-color: black;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .selected-color {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: var(--selected-color);
  }
  .color-squares,
  button {
    position: relative;
    z-index: 2;
  }
  
  .heading {
    position: relative;
    z-index: 2;
    font: bold;
    font-size: xx-large;
  }
  
  .App {
    text-align: center;
  }